@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.session-metadata-wrapper {
  .session-metadata-inner {
    max-height: 200px;
    overflow: hidden;
  }

  .username-container {
    max-width: 300px;

    .username {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .owner-label {
      white-space: nowrap;
    }
  }

  .custom-tags {
    column-gap: 20px;

    .tag {
      padding: 6px 16px;
      border-radius: 8px;
      white-space: nowrap;

      &.tag-categorize {
        background-color: color.adjust(pick-color('common', 'danger'), $lightness: 40%);
        color: pick-color('common', 'danger');
      }

      &.tag-category {
        background-color: color.adjust(pick-color('secondary', 'blue'), $lightness: 35%);
        color: pick-color('secondary', 'blue');
      }

      &.tag-location {
        display: flex;
        align-items: center;
        column-gap: 8px;
        color: white;
        background-color: pick-color('primary', 'web');

        svg {
          width: 20px;
          height: 20px;
          flex-shrink: 0;
        }
      }
    }

    .dropdown-toggle-wrapper {
      span {
        font-weight: 400;
        white-space: nowrap;
      }

      .dropdown-toggle-btn {
        @include circles(32px, pick-color('primary', 'web'));
        border-radius: 4px !important;
        transition: all 0.2s ease;

        .toggle-icon {
          width: 20px;
          height: 20px;
          font-size: 20px;
          transition: all 0.2s ease;
          color: white;
        }

        &.toggled .toggle-icon {
          transform: rotate(-180deg);
        }
      }
    }
  }

  .metadata-content-wrapper {
    width: 100%;
    border-radius: 8px;
    border: 1px solid pick-color('primary', 'fog');
    background-color: white;
    margin-top: 20px;
    padding: 16px;
    max-height: 160px;
    overflow: hidden;
    transition: max-height 0.5s ease;

    &[aria-expanded='true'] {
      // max-height: 1500px;
      max-height: 400px;
    }

    .metadata {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      gap: 16px;

      .metadata-item {
        mat-icon {
          width: 24px;
          height: 24px;
          margin-bottom: 2px;
        }
      }
    }

    .metadata-form {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: repeat(6, 1fr);
      gap: 16px;

      .form-description {
        grid-row: span 3;
        height: 100%;
      }
    }
  }
}
